import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';

import { ApiResp, IWholesaleApi, WholesaleData, WholesaleResp } from './types';

export default class WholesaleApi implements IWholesaleApi {
	private readonly _apiStore = RootStore.apiStore;

	async wholesaleSend(data: WholesaleData): Promise<ApiResp> {
		const response = await this._apiStore.request<ApiResponse<WholesaleResp, WholesaleResp>>({
			method: HTTPMethod.POST,
			endpoint: `/api/v1/request/wholesale`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
			data,
		});
		try {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}

	async wholesaleChildrenSend(data: WholesaleData): Promise<ApiResp> {
		const modData = { ...data, category: 'kids' };
		const response = await this._apiStore.request<ApiResponse<WholesaleResp, WholesaleResp>>({
			method: HTTPMethod.POST,
			endpoint: `/api/v1/request/wholesale`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
			data: modData,
		});
		try {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}
}
